import { Container, createTheme, CssBaseline, Grid, InputLabel, ThemeProvider } from '@mui/material';
import { blue, purple } from '@mui/material/colors';
import { dark } from '@mui/material/styles/createPalette';
import { React, useEffect, useState } from 'react'
import Code from './Code/Code';
import Result from './Result/Result';
import SelectLang from './SelectLang/SelectLang';
import Submit from './Submit/Submit';
import Upload from './Upload/Upload';
import { Label } from '@mui/icons-material';

const App = () => {

  const [code, setCode] = useState("");
  const [result, setResult] = useState("");
  const [lang, setLang] = useState("");

  const [cache, setCache] = useState({});

  const theme = createTheme({
    palette: {
      mode: "dark",
      primary: {
        main: blue[500],
      },
      secondary: {
        main: '#f44336',
      },
    }
  })

  return (
    <>
     <ThemeProvider theme={createTheme(theme)}>
      <CssBaseline enableColorScheme/>
        <h1>
          Compilatore online
        </h1>
            <Grid container marginY={4} alignItems={"center"}
              columnSpacing={6}>
              <Grid item xs={4} md={3}>
                <SelectLang lang={[lang, setLang]} cache={[cache, setCache]}/>
              </Grid>
              <Grid item xs={5} md={7} lg={8}>
                <Upload setCode={setCode}/>
              </Grid>
              <Grid item>
                <Submit code={code} lang={lang} setResult={setResult}/>
              </Grid>
            </Grid>
            <Grid container justifyContent={"center"}>
              <Grid item xs={12} marginBottom={4}>
                <Code result={[code, setCode]} lang={lang}/>
              </Grid>
            </Grid>
            Output:
            <Grid container wrap={"wrap"} justifyContent={"center"}>
              <Result result={result}/>
            </Grid>
     </ThemeProvider>
    </>
  )
}

export default App