import { Button, Fab } from '@mui/material'
import React from 'react'

const config = require("../config.json")

const Submit = (props) => {

  const send = () => {
    fetch(config.URLs.compilersURL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        "code": props.code,
        "language": props.lang,
        "input": ""
      })
    }).then((response) => response.json())
    .then((json_text) => {
      props.setResult(json_text);
    })
  }

  return (
    <Fab onClick={send} variant="extended" color='primary' sx={
      {
        position: "fixed"
      }
    }>
      Run
    </Fab>
  )
}

export default Submit