import { FileUpload, PhotoCamera } from '@mui/icons-material'
import { IconButton } from '@mui/material'
import React from 'react'

const Upload = (props) => {

    const changed = async (event) => {
        let f = event.target.files[0];
        props.setCode(await f.text())
    }

  return (
    <IconButton color="primary" aria-label="upload file" component="label"
    sx={
        {
            fontSize: 16
        }
    }>
        <input hidden type="file" onChange={changed}/>
        Upload a file
        <FileUpload sx={{ml: 2}}/>
    </IconButton>
  )
}

export default Upload