import React, { useEffect, useState, useLayoutEffect } from 'react'

import { FormControl, InputLabel, Select, MenuItem, Grid, Alert, AlertTitle} from '@mui/material'

const config = require("../config.json");

const SelectLang = (props) => {

    const [langs, setLangs] = useState([]);    
    const [error, setError] = useState(false);

    useLayoutEffect(() => {
        if(props.cache[0].langList === undefined){
            fetch(config.URLs.compilersListURL)
            .then((text) => text.json())
            .catch((reason) => alert(reason))
            .then((json_text) => {
                if(json_text.status === 200){
                    setLangs(json_text.supportedLanguages);
                    props.cache[1]((obj) => {
                        return {
                            ...obj,
                            langList: json_text.supportedLanguages
                        }
                    });
                }else{
                    setError(true);
                }
            });
        }
        else{
            setLangs(props.cache[0].langList);
        }
        
    }, []);

    const langSelected = (event) => {
        props.lang[1](event.target.value);
    }

    return (
        <>
            <FormControl fullWidth>
                <InputLabel id="SelectLang-langselectlabel">Language</InputLabel>
                <Select
                    labelId="SelectLang-langselectlabel"
                    id="SelectLang-langselect"
                    label="Language"
                    value={props.lang[0]}
                    onChange={langSelected}
                >
                    {
                        langs.map((e, i) => {
                            return <MenuItem key={i} value={e.language}>{e.language}</MenuItem>
                        })
                    }
                </Select>
            </FormControl>
        </>
    )
}

export default SelectLang